import sstyle from './RandomCircleStyle.module.css'
import { RandomColor } from '../../Blogic/CreateRandomColor';

const GetRandomColor = () => {

    document.documentElement.style.setProperty("--backgroundColor",RandomColor);
}
GetRandomColor();
const RandomCircle = () => {
    return (<div className={sstyle.circleContainer}>

        <div className={sstyle.circle}><div className={sstyle.text}>  {"Color: " + RandomColor}</div></div>
    </div>
    )
}
export default RandomCircle
export { RandomCircle,RandomColor };
